import orderDetailsStyles from './order-details.module.css';
import doneImagePath from '../../images/done.svg';
import { FC } from 'react';

const OrderDetails: FC = () => {
  return (
    <>
      <h5 className={`${orderDetailsStyles.title} text text_type_main-medium mt-8`}> </h5>
      <img className="mt-15" src={doneImagePath} alt="  " />
      <p className="text text_type_main-default mt-15">   </p>
      <p className={`${orderDetailsStyles.paragraph} text text_type_main-default mt-2 mb-30`}>    </p>
    </>
  )
}

export default OrderDetails;